import Vue from 'vue'
import Vuex from 'vuex'

import user from './module/user'
import monitor from './module/monitor'
import place from './module/place'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    topTitle: '智能物联网大数据平台',
    showLoadingDiolod: false
  },
  mutations: {
    setTitle(state, data) {
      state.topTitle = data
    },
    setShowLoadingDiolod(state, data) {
      state.showLoadingDiolod = data
    }
  },
  actions: {
    //
  },
  modules: {
    user,
    monitor,
    place
  }
})

export default store

