    <div class="logo">99物联</div>
    <div class="title">{{topTitle}}</div>
    <div class="user">
      <span class="iconfont iconuser"></span>
      <span class="name">{{username}}</span>
      <span class="iconfont icontuichu logout" @click="handleLogout"></span>
    </div>
    <div class="blur"></div>
  </div>
</template>

<script>
import { getItem } from '@/utils/funcs'
import constant from '@/common/constant'
import { unAuth } from '@/api/user'
export default {
  name: 'TopHeader',
  data() {
    return {
    }
  },
  computed: {
    username() {
      return getItem(constant.sysuser) || 'username'
    },
    topTitle() {
      return this.$store.state.topTitle || '智能物联网大数据平台'
    }
  },
  methods: {
    handleLogout() {
      unAuth()
      window.localStorage.clear()
      this.$router.push({ path: '/login' })
    }
  }
}
</script>

