<template>
    <div class="side-menu">
      <ul>
        <li v-for="(item, index) in menu" :key="index" :class="{active: isActive(item.route, index)}" @mouseover="handleShowSubmenu(index)" @mouseout="handleHideSubmenu(index)" @click="handleClick(item)">
          <img :src='imagesrc(item.icon)' alt="">
          <p>
            {{item.name}}
          </p>
          <div class="submenu company" v-show="showEquip" v-if="index === 2">
            <img src="../../assets/img/single.png" class="single" alt="">
            <div class="sub-items">
              <el-input v-model="search" placeholder="搜索"></el-input>
              <div class="sub-item" v-for="(i, k) in listData" :class="{active: isActiveCompany(i.id)}" :key="k">
                <router-link :to="{name: 'equipment-company-id', params: {id: i.id}}">{{i.name}}</router-link>
              </div>
            </div>
          </div>
          <div class="submenu" v-show="showSubmenu[index]" v-if="item.children && item.children.length !== 0">
            <img src="../../assets/img/single.png" class="single" alt="">
            <div class="sub-items">
              <div class="sub-item" :class="{active: subActive(i.pathname)}" v-for="(i, k) in item.children" :key="k">
                <router-link :to="{name: i.pathname}">{{i.name}}</router-link>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
import menu from '@/router/menu'
import Vue from 'vue'
import _ from 'lodash'
import { findPlaceList } from '@/api/place'

export default {
  name: 'SideMenu',
  mounted() {
    this.showSubmenu = this.menu.map(i => false)
    this.getPlaceList()
  },
  computed: {
    subActive() {
      return (pathname) => {
        return this.$route.name === pathname
      }
    },
    isActiveCompany() {
      return (id) => {
        return +this.$route.params.id === +id
      }
    },
    isActive() {
      return (routename, index) => {
        return this.$route.meta.parent === routename || this.showSubmenu[index]
      }
    },
    menu() {
      return menu
    },
    imagesrc() {
      return (src) => {
        return require(`../../icons/${src}.png`)
      }
    },
    companylist() {
      return this.$store.state.companylist || []
    }
  },
  data() {
    return {
      showSubmenu: [],
      showEquip: false,
      search: '',
      listData: []
    }
  },
  methods: {
    handleShowSubmenu(index) {
      if (index == 2) {
        this.showEquip = true
      }
      Vue.set(this.showSubmenu, index, true)
    },
    handleHideSubmenu(index) {
      if (index == 2) {
        this.showEquip = false
      }
      Vue.set(this.showSubmenu, index, false)
    },
    handleClick(menu) {
      if (menu.route === 'equipment') {
        return
      }
      if (!_.has(menu, 'children')) {
        this.$router.push({ path: menu.path })
      }
    },
    async getPlaceList() {
      const params = {
        page: 1,
        per_page: 99999999999
      }
      const { data } = await findPlaceList(params)
      this.listData = data.items
    }
  }
}
</script>

